.securityCenter .main {
  margin: 1.5625rem;
}
.securityCenter .main .mainTitle {
  font-size: 1.125rem;
  color: #fff;
}
.securityCenter .main .mainContent {
  margin-top: 0.9375rem;
}
.securityCenter .main .mainContent .accountInformation {
  justify-content: space-between;
  color: #fff;
  display: flex;
  align-items: center;
  height: 2.5rem;
  position: relative;
}
.securityCenter .main .mainContent .accountInformation:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #eee;
  color: #eee;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.securityCenter .main .mainContent .accountInformation .leftMessage {
  color: #666;
}
.securityCenter .main .footer {
  position: fixed;
  left: 0px;
  bottom: 3.125rem;
  width: 100%;
  color: #00D2FF;
  text-align: center;
}
.contactCustomer .rightMessage {
  color: #00D2FF;
}
.contactCustomer .footerMessage {
  color: #666;
  padding-top: 0.9375rem;
}
.aboutUs .main .mainContent .leftMessage {
  color: #fff !important;
}
